import React from 'react'
import Page from 'components/layout/Page'
import { Heading } from 'uikit'
import { Route, useRouteMatch } from 'react-router-dom'
import FlexLayout from 'components/layout/Flex'
import { PegType } from 'config/constants/types'
import { BoardTabButtons } from 'views/Boardroom/Boardroom'
import BondToken from './BondToken'

const Bond: React.FC = () => {
  const { path } = useRouteMatch()

  return (
    <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
          ðŸ’° Bonds
        </Heading>
        <BoardTabButtons />
      </FlexLayout>
      <Route exact path={`${path}`}>
        <BondToken type={PegType.SDO} />
      </Route>
      <Route exact path={`${path}/sbnb`}>
        <Heading size="lg">Soon...</Heading>
      </Route>
    </Page>
  )
}

export default Bond
