import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import BigNumber from 'bignumber.js'
import Spacer from 'components/Spacer'
import { useBondStats, useCashPriceInEstimatedTWAP } from 'hooks/useExitBoardroom'
import { getDisplayBalance } from 'utils/formatBalance'
import { useTreasury } from 'hooks/useContract'
import UnlockButton from 'components/UnlockButton'
import useTokenBalance from 'hooks/useTokenBalance'
import { PegType, QuoteToken } from 'config/constants/types'
import ExchangeCard from './components/ExchangeCard'
import ExchangeStat from './components/ExchangeStat'

const BOND_REDEEM_PRICE_BN = new BigNumber(10).pow(18).multipliedBy(101).dividedBy(100)

const BondToken: React.FC<any> = ({ type }) => {
  const { account } = useWallet()
  const contract = useTreasury(type)
  const bondBalance = useTokenBalance(QuoteToken.SBO)
  const cashStat = useCashPriceInEstimatedTWAP(type)
  const [burnable, redeemable] = useBondStats(type)

  const token = useMemo(() => type === PegType.SDO ? QuoteToken.SDO : QuoteToken.SBNB, [type])

  const handleBuyBonds = useCallback(
    async (amount: string) => {
      if (!amount) return
      const amountBig = new BigNumber(amount).times(new BigNumber(10).pow(18))
      const priceBig = new BigNumber(cashStat).times(new BigNumber(10).pow(18))
      await contract.methods.buyBonds(amountBig, priceBig).send({ from: account })
    },
    [cashStat, account, contract.methods],
  )

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      if (!amount) return
      const amountBig = new BigNumber(amount).times(new BigNumber(10).pow(18))
      const priceBig = new BigNumber(cashStat).times(new BigNumber(10).pow(18))
      await contract.methods.redeemBonds(amountBig, priceBig).send({ from: account })
    },
    [cashStat, account, contract.methods],
  )
  const isBondRedeemable = useMemo(() => cashStat.gt(BOND_REDEEM_PRICE_BN) && Number(redeemable) > 0, [
    cashStat,
    redeemable,
  ])
  const isBondPurchasable = useMemo(() => Number(cashStat) < 1.0, [cashStat])
  const description = useMemo(() => (isBondPurchasable ? 'Burnable Dollars' : 'Redeemable Bonds'), [isBondPurchasable])

  return account ? (
    <>
      <StyledBond>
        <StyledCardWrapper>
          <ExchangeCard
            action="Purchase"
            fromToken={token}
            toToken={QuoteToken.SBO}
            priceDesc={
              !isBondPurchasable
                ? `${token} is over peg`
                : `${Math.floor(100 / cashStat.toNumber() - 100)}% return when ${token} is over`
            }
            onExchange={handleBuyBonds}
            disabled={!isBondPurchasable}
            type={type}
          />
        </StyledCardWrapper>
        <Spacer />
        <StyledCardWrapper>
          <ExchangeStat
            description={description}
            label={isBondPurchasable ? burnable?.toFixed(3) : redeemable?.toFixed(3)}
          />
          <Spacer />
          <ExchangeStat
            description="Last-Hour TWAP Price"
            label={`ðŸ’° ${token} = $${cashStat?.toFixed(3) || '-'}`}
          />
        </StyledCardWrapper>
        <Spacer />
        <StyledCardWrapper>
          <ExchangeCard
            action="Redeem"
            fromToken={QuoteToken.SBO}
            toToken={token}
            priceDesc={`${getDisplayBalance(bondBalance)} ${token} Available`}
            onExchange={handleRedeemBonds}
            disabled={!isBondRedeemable}
            disabledDescription={isBondRedeemable ? `Enabled when ${token} over peg` : 'Redeem'}
            type={type}
          />
        </StyledCardWrapper>
      </StyledBond>
    </>
  ) : (
    <UnlockButton mt="8px" fullWidth />
  )
}

const StyledBond = styled.div`
  display: flex;
  width: 960px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`

export default BondToken
